  .wrap {
    border-right: .3vw solid #fff;
    color: #fff;
    animation: caret 1s steps(1) infinite;
    position: relative;
  }
   
  @keyframes caret {
    50% {
      border-color: transparent;
    }
  }

  @keyframes slideIn {
    0% {
      -moz-transition: translatey(3000%);
      -o-transition: translatey(300%);
      -webkit-transform: translatey(300%);
      transform: translatey(300%);
    }
    33% {
      -webkit-transform: translatey(300%);
      -moz-transition: translatey(300%);
      -o-transition: translatey(300%);
      transform: translatey(300%);
    }
    100% {
      -moz-transition: translatey(0%);
      -o-transition: translatey(0%);
      -webkit-transform: translatey(0);
      transform: translatey(0);
    }
    }
    @keyframes mainText {
      0% {
        opacity: 0;
        visibility: visible;
      }
      50% {
        opacity: 0;
        visibility: visible;
      }
      100% {
        opacity: 1;
        visibility: visible;
      }
  }